<template>
  <div id="Visitor">
    <h3>最近访客</h3>
    <ul>
      <li
        v-for="(item, index) in visitorList"
        :key="index"
        :style="{
          backgroundImage: `url(${baseURL}${item.visitor.photo})`
        }"
      >
        <p>{{ item.visitor.user }}</p>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "Visitor",
  data() {
    return {
      visitorList: [
        // { user: "访客1", photo: require("../../assets/img/tx1.png")},
        // { user: "访客2", photo: require("../../assets/img/tx2.png")},
        // { user: "访客3", photo: require("../../assets/img/tx3.png")},
        // { user: "访客4", photo: require("../../assets/img/tx4.png")},
        // { user: "访客5", photo: require("../../assets/img/tx5.png")},
        // { user: "访客6", photo: require("../../assets/img/tx6.png")},
        // { user: "访客7", photo: require("../../assets/img/tx7.png")},
        // { user: "访客8", photo: require("../../assets/img/tx8.png")},
        // { user: "访客9", photo: require("../../assets/img/tx9.png")},
        // { user: "访客10", photo: require("../../assets/img/tx10.png")},
        // { user: "访客11", photo: require("../../assets/img/tx11.png")},
        // { user: "访客12", photo: require("../../assets/img/tx12.png")},
      ],
    };
    
  },
  async created() {
    let {data} = await this.$axios({
      method: "GET",
      url: "/visitor"
    })

    if (data.code)return

    this.visitorList = data.data
  }
};
</script>
<style lang="less">
#Visitor {
  border-radius: 5px;
  width: 270px;
  height: 250px;
  border: 1px solid #111;
  box-sizing: border-box;
  padding: 15px;
  overflow: hidden;
  background-color: #e1e1e1;
  h3 {
    height: 35px;
    border-bottom: 1px Dotted #333;
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 10px;
    li {
      position: relative;
      width: 53px;
      height: 53px;
      margin-bottom: 6px;
      background-size: cover;
      background-position: center top;
      background-color: aquamarine;
      p {
        position: absolute;
        bottom: 0;
        font-size: 12px;
        text-align: center;
        height: 15px;
        width: 100%;
        overflow: hidden;
        color:#fff;
        background-color: rgba(0, 0, 0, 0.3);
      }
    }
  }
}
</style>