<template>
  <div id="LoginBox" @Click.self="boxClick">
    <div class="BoxMain">
      <el-tabs v-model="activeName">
        <el-tab-pane label="用户登陆" name="login">
          <el-form
            :model="loginForm"
            :rules="rules"
            ref="loginForm"
            label-width="80px"
          >
            <el-form-item label="用户名:" prop="user">
              <el-input v-model="loginForm.user"></el-input>
            </el-form-item>
            <el-form-item label="密码:" prop="pass">
              <el-input type="password" v-model="loginForm.pass"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="loginSubmit">登陆</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="注册账号" name="register">
          <el-form
            :model="regForm"
            :rules="rules"
            ref="regForm"
            label-width="80px"
          >
            <el-form-item label="用户名:" prop="user">
              <el-input v-model="regForm.user"></el-input>
            </el-form-item>
            <el-form-item label="密码:" prop="pass">
              <el-input type="password" v-model="regForm.pass"></el-input>
            </el-form-item>
            <el-form-item label="确认密码:" prop="pass2">
              <el-input type="password" v-model="regForm.pass2"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="regSubmit">立即注册</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
import { mapMutations } from "vuex";

export default {
  name: "LoginBox",
  emits: ["loginSuccess"],
  data() {
    return {
      activeName: "login",
      regForm: {
        user: "",
        pass: "",
        pass2: "",
      },
      loginForm: {
        user: "",
        pass: "",
      },
      //规则
      rules: {
        //验证用户名
        user: [
          {
            //验证器
            validator(rule, value, callback) {
              let reg = /^[^\s]{2,8}$/;

              if (reg.test(value)) {
                callback();
              } else {
                callback(new Error("2-8位非空格字符"));
              }
            },
            trigger: "blur",
          },
        ],
        pass: [
          {
            validator(rule, value, callback) {
              let reg = /^[\w,.?;<>/|\\:'"!@#$%^&*()+-]{6,16}$/;
              if (reg.test(value)) {
                callback();
              } else {
                callback(new Error("6-16位字符"));
              }
            },
            trigger: "blur",
          },
        ],
        pass2: [
          {
            //需要拿到前面密码的值，箭头函数没有this，this就指向整个实例
            validator: (rule, value, callback) => {
              if (!value) {
                callback(new Error("密码不能为空"));
              } else if (value !== this.regForm.pass) {
                callback(new Error("两次输入密码不一致"));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    //vuex
    ...mapMutations(["updateUserInfo"]),

    //注册
    regSubmit() {
      this.$refs.regForm.validate(async (valid) => {
        if (valid) {
          /*将数据发送到后端*/
          let response = await this.$axios({
            method: "post",
            url: "/reg",
            data: {
              user: this.regForm.user,
              pass: this.regForm.pass,
            },
          });
          

          //注册失败
          if(response.data.code){
            return this.$message.error(response.data.msg)
          }

          //注册成功   element Mseeage加样式
          this.$message({
            type:"success",
            message:"注册成功！",
            duration:500
          })
          // this.$message.success("注册成功！")


          //切换到登录页面
          this.activeName = "login"

          //清空注册表单的内容  resetFields()是element表单重置函数
          this.$refs.regForm.resetFields()
        } else {
          return false;
        }
      });
    },
    
    //登陆按钮点击
    loginSubmit() {
      this.$refs.loginForm.validate(async (valid) => {
        if (valid) {
          //发送数据给后端
          let response = await this.$axios({
            method: "post",
            url: "/login",
            data: this.loginForm
          });

          //没有登陆成功
          if (response.data.code) {
            return this.$message.error(response.data.msg);
          }

          //登陆成功
          this.$message.success("登陆成功！");
          //修改父组件的数据，关闭掉弹窗 抛出自定义事件让父组件接收
          this.loginForm.user = "",
          this.loginForm.pass = "",
          this.$emit("loginSuccess");
          //修改Vuex的数据
          this.updateUserInfo(response.data.data);
        } else {
          return false;
        }
      });
    },
  },
};
</script>
<style lang="less" scope >
#LoginBox {
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 22;
  div.BoxMain {
    width: 320px;
    height: 300px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); //偏移平移xy
    background-color: #fff;
    padding: 15px;
  }
}
</style>