<template>
  <div id="AppSide">
    <Personal></Personal>
    <div class="fixed" :class="{ true: ifFixed }">
      <HotArticle></HotArticle>
      <Visitor></Visitor>
    </div>
  </div>
</template>
<script>
import Personal from "@/components/AppSide/Personal";
import HotArticle from "@/components/AppSide/HotArticle";
import Visitor from "@/components/AppSide/Visitor";

export default {
  name: "AppSide",
  components: {
    Personal,
    HotArticle,
    Visitor,
  },
  data() {
    return {
      ifFixed: "",
    };
  },
  methods: {
    scrollEventFn() {
      let top = document.documentElement.scrollTop || document.body.scrollTop;
      this.ifFixed = top > 238 ? "true" : "";
    },
  },
  mounted() {
    this.scrollEventFn();
    window.addEventListener("scroll", this.scrollEventFn);
  },
};
</script>
<style lang="less" scoped>
#AppSide {
  width: 270px;
  margin-right: 15px;
  div.fixed.true {
    position: fixed;
    top: 65px;
  }
}
</style>