import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import installElementPlus from './plugins/element'
import axios from "axios"

const app = createApp(App)
installElementPlus(app)
app.config.globalProperties.$axios = axios



//判断是否是开发环境
let ifDEV = process.env.NODE_ENV === "development"
const baseURL = ifDEV?"http://localhost:5000":""

axios.defaults.baseURL = baseURL

//axios默认不懈怠cookie，session是根据携带的cookie来判断是否登录
axios.defaults.withCredentials = true //让axios携带cookie

app.use(store).use(router).mixin({
  data() {
    return {
      baseURL,
      session:"",
    }
  }
}).mount('#app')