<template>
  <div id="AppNav">
    <div class="main">
      <div class="logo">
        <a href="/"><img src="../assets/logo.png" alt="" /></a>
      </div>
      <nav>
        <ul>
          <li><router-link to="/">首页</router-link></li>
          <li><router-link to="/Article">文章</router-link></li>
          <li><router-link to="/Message">留言</router-link></li>
          <li><router-link to="/Link">友链</router-link></li>
          <li><router-link to="/About">关于</router-link></li>
        </ul>
      </nav>

      <!-- 头像 -->
      <div id="UserPhoto" v-if="userInfo.user">
        <el-popover placement="bottom" :width="170" trigger="click">
          <template #reference>
            <div
              class="photo"
              :style="{
                backgroundImage: `url(${baseURL}${userInfo.photo})`,
              }"
            ></div>
          </template>

          <div class="btn">
            <el-button type="primary" size="mini" @click="userUpdate"
              >修改资料
            </el-button>
            <el-button class="second" type="danger" size="mini" @click="logout"
              >退出登录
            </el-button>
          </div>
        </el-popover>
      </div>

      <!-- 登录注册按钮 -->
      <div class="login" v-else>
        <el-button type="success" size="mini" @click="loginBtnClick"
          >登陆 / 注册</el-button
        >
      </div>

      <LoginBox
        v-show="ifShowLoginBox"
        @loginSuccess="loginSuccess"
        @click.self="closeBox"
      ></LoginBox>
    </div>
  </div>
</template>
<script>
// console.log(userInfo)
import LoginBox from "@/components/LoginBox.vue";
import { mapState, mapMutations } from "vuex";

export default {
  name: "AppNav",
  data() {
    return {
      ifShowLoginBox: false,
    };
  },
  components: {
    LoginBox,
  },
  computed: {
    ...mapState(["userInfo"]),
  },
  methods: {
    ...mapMutations(["updateUserInfo"]),
    loginBtnClick() {
      this.ifShowLoginBox = true;
    },

    //接收子组件自定义事件改变数据
    loginSuccess() {
      this.ifShowLoginBox = false;
    },

    closeBox() {
      this.ifShowLoginBox = false;
    },

    //修改资料
    userUpdate() {
      //跳转到user路由
      this.$router.push("/user");
    },

    //退出登录
    async logout() {
      await this.$axios({
        method: "post",
        url: "/login/logout",
      });
      this.$message.success("已退出登录");
      
      

      //清除vuex的数据
      this.updateUserInfo({});
      this.$router.replace("/");
    },
  },
};
</script>
<style lang="less" scope>
#AppNav {
  width: 100%;
  height: 50px;
  border: 1px solid #bbb;
  min-width: 700px;
  position: fixed;
  top: 0;
  background-color: #e1e1e1;
  z-index: 21;
  div.main {
    max-width: 1300px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    padding: 0 11px;
    box-sizing: border-box;
    line-height: 50px;
    .logo {
      width: 130px;
      height: 50px;
      margin: 12px 0 0 12px;
      a {
        img {
          width: 30px;
        }
      }
    }
    nav ul {
      display: flex;
      height: 50px;
      line-height: 50px;
      li {
        font-weight: bolder; //字体
        height: 50px;

        a {
          display: block;
          height: 98%;
          border-bottom: 2px solid transparent;
          padding: 0 25px;
          color: #777;
          line-height: 48px;
          font-weight: bolder;
          transition: all 0.3s;
          text-decoration: none;
          &:hover {
            color: rgb(55, 144, 185);
          }
          &.router-link-active {
            //点击
            color: #00b7ff;
            border-color: #00b7ff;
          }
        }
      }
    }
    .login {
      margin-right: 15px;
    }
  }
  #UserPhoto {
    div.photo {
      width: 40px;
      height: 40px;
      background-size: cover;
      background-position: center top;
      border-radius: 50%;
      margin-top: 5px;
      margin-right: 15px;
    }
    div.btn {
      display: inline-block;
    }
  }
}
</style>